/* @flow */
var re = /^dotenv_config_(encoding|path|debug)=(.+)$/;

module.exports = function optionMatcher(args
/*: Array<string> */
) {
  return args.reduce(function (acc, cur) {
    var matches = cur.match(re);

    if (matches) {
      acc[matches[1]] = matches[2];
    }

    return acc;
  }, {});
};